.form__group {
    margin-bottom: 1.5rem;
    position: relative;
}

.form__control {
    display: block;
    width: 100%;
    padding: .5rem 0;

    background: none;
    border: none;
    border-bottom: 1px solid #3F3F3F;
    border-radius: 0;
    box-shadow: none;

    font-family: inherit;
    font-size: 1.4rem;
    @include themed() {
        color: t("whites");
    }

    &::placeholder {
        @include themed() {
            color: t("darkenWhite");
        }   
    }

    &:focus + .form__line {
        width: 100%;
    }

    &--textarea {
        height: 35px;
        overflow: hidden;
        resize: none;
    }
}

.form__line {
    width: 0;
    height: 1px;

    background-color: $cobalt;

    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 1;

    transition: width .2s linear;
}
